.app-header {
    padding: 20px;
    position: sticky;
    background-color: #f7ebd4e1;
    top: 0;
    z-index: 5;
    @media screen and (max-width: 800px) {
        justify-content: space-between;
    }
    
    i{
        display: none;
        @media screen and (max-width: 800px) {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.8rem;
        }

    }


    nav {
       
        font-size: .8em;
        padding: 5px;
        justify-content: center;
        align-items: center;
        a{
            font-weight: 700;
            font-size: rem(20px);
            color: #595959;
            transition: 0.4s;
            padding: 0 5px;
            margin: 10px;
            &:hover{
                color: #dd3c3c;
            }
        }
        
        @media screen and (max-width: 800px) {
            position: fixed;
            top: 0;
            right: 0;
            flex-direction: column;
            justify-content: space-evenly;
            background-color:rgba(41, 41, 41,0.9) ;
            height: 100vh;
            box-shadow: 0px 0px 0px;
            transform: translateX(100%);
            transition: transform .6s;
             width: 150px;
             a{
                 color: rgba(255, 255, 255, 0.74);
             }
 
         }
    }
   
    
}

body.manu-open {
    i{
        display: none;
    }
}

body.manu-open nav{
    @media screen and (max-width: 800px) {
    box-shadow: -8px 0px 20px #0003;
    transform: translateX(0%);  
    //  pointer-events: none; 
    i{
        display: block;
    }
    }
}
