
.about-info{
    @media screen and (max-width: 800px) {
        padding-left: 5px;
    }
    h1{
        margin-bottom: 40px;
    }
}
.skills{
    @media screen and (max-width: 800px) {
        padding-left: 5px;
    }
    margin-bottom: 120px;
    h2{
        margin-bottom: 40px;
    }
    ul{
        list-style: none;
        gap:20px;
        flex-wrap: wrap;
        li{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 120px;
            padding: 15px;
            border-radius: 10px;
            cursor: default;
            transition: 0.4s;
            &:hover{
                transform: rotate(-30deg);
            }

            &:nth-of-type(1){
                background-color: #f94144 ;
            }
            &:nth-of-type(2){
                background-color: #f3722c ;
            }
            &:nth-of-type(3){
                background-color: #f8961e ;
            }
            &:nth-of-type(4){
                background-color: #f9844a ;
            }
            &:nth-of-type(5){
                background-color: #f9c74f ;
            }
            &:nth-of-type(6){
                background-color: #90be6d ;
            }
            &:nth-of-type(7){
                background-color: #43aa8b ;
            }
            &:nth-of-type(8){
                background-color: #4d908e ;
            }
            &:nth-of-type(9){
                background-color: #577590 ;
            }
            &:nth-of-type(10){
                background-color: #277da1 ;
            }
            
        }
    }
}

.space{
    border-bottom: 1px solid rgba(122, 122, 122, 0.89);
    width: 400px;
    margin: 60px auto;
    @media screen and (max-width: 800px) {
        width: 250px;
    }
}