
.hero{
    @media screen and (max-width: 800px) {
        margin-bottom: 40px;
    }
    text-align: center;
    margin-top: 40px;
    h1{
        @media screen and (max-width: 800px) {
            font-size: 4rem ;
            width: auto; 
        }
        font-size: 5rem ;
        font-family:"Source Code Pro",monospace;
        position: relative;
         width: max-content; 
         letter-spacing: 10px;
        &::before,
        &::after{
            content: "";
            top:0;
            right: 0;
            left: 0;
            bottom: 0;
            position: absolute;
        }

        &::before{
            background-color:$bg-color;
            animation: 
            typewriter $typewriterSPEED steps($typewriterCarectres) 1s forwards;
        }
        
        &::after{
            width: 0.125em;
            background-color: black;
            animation: 
            typewriter $typewriterSPEED steps($typewriterCarectres) 1s forwards,
            blink 800ms steps($typewriterCarectres) infinite ;
        }

    }

}
.subtitle{
    color: rgba(0, 0, 0, 0.7); 
    font-size: 2rem;
    opacity: 0;
    transform: translateY(3rem);
    animation: 
    fadeInUp 3s ease $typewriterSPEED + 1s forwards;
    h2,h3{
        font-weight: 300;
    }
    h3{
        @media screen and (max-width: 800px) {
            font-size: 2rem ; 
        }
    }
 }




// Animations

@keyframes typewriter{
    to{
        left: 100%;
    }
}

@keyframes blink  {
    to{
        background: transparent;
        background: black;
    }
    
}

@keyframes fadeInUp  {
    to{
        opacity: 1;
        transform: translateY(0);

    }
}
