.text-continer{
    @media screen and (max-width: 800px) {
        width: 350px;
    }
    width: 600px;
    margin-bottom: 20px;
    h2{
        @media screen and (max-width: 800px) {
            font-size: 1.8rem;
        }
        font-size: rem(40px);
    }
    p{
        font-size: rem(20px);
        &:last-of-type{
            font-weight: 700;
            font-size: rem(22px);
            text-transform: capitalize;
        }
    }
    span{
        a{
            text-decoration: brown underline;
            color: black;
        }
    }
}

.img-continer{
    @media screen and (max-width: 800px) {
        margin-bottom: 40px;
    }
    padding-top: 10px;
    height: 320px;
    img{
        border: 1px solid rgba(100, 100, 100, 0.74);
        width: 100%;
        height: 100%;
        border-radius: 10px;
        box-shadow: 4px 4px 6px 3px #6464647d;
    }
}

.icon-slection-continer{
    width: 500px;
    @media screen and (max-width: 800px) {
        padding-left: 30px;
    }
    .icon-slection{
        list-style: none;
        padding: 0;
        gap: 50px;
        li{
            background-color: white;
            border: 1px solid rgba(0, 0, 0, 0.521);
            width: 60px;
            height: 60px; 
            border-radius: 50%;  
            transition: 0.4s;
            a{
                text-decoration: none;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;   
                text-align: center;
                cursor: pointer;
            }
            .MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium{
                font-size: rem(38px);
                &.linkedIn{
                    color: #0A66C2;
                }
                &.gitHub{
                    color: black;
                }
                &.facebook{
                    color: #4867AA;
                }
            } 
            
           &:hover{
            background-color: black;
            .MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium{
              color: brown;
            } 
           }
         }
    }
}

.info-continer{
    @media screen and (max-width: 800px) {
        flex-direction: column;
        gap: 10px;
    }
}